:root {
  /* Enhanced Color Palette */
  --primary-color: #3498db;
  --primary-light: #5dade2;
  --primary-dark: #2980b9;
  --secondary-color: #2ecc71;
  --secondary-light: #58d68d;
  --secondary-dark: #27ae60;
  --accent-color: #e74c3c;
  --accent-light: #ec7063;
  --accent-dark: #c0392b;
  --purple-color: #9b59b6;
  --purple-light: #bb8fce;
  --purple-dark: #8e44ad;
  --orange-color: #f39c12;
  --orange-light: #f5b041;
  --orange-dark: #d68910;
  --background-light: #f8f9fa;
  --background-dark: #ecf0f1;
  --text-color: #2c3e50;
  --text-muted: #7f8c8d;
  --success-color: #27ae60;
  --warning-color: #f1c40f;
  --danger-color: #e74c3c;
  --info-color: #3498db;
}

/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
  padding: 20px;
}

.app-container {
  font-family: 'Segoe UI', Roboto, Arial, sans-serif;
  padding: 30px;
  background-color: var(--background-light);
  color: var(--text-color);
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Header Styles */
.app-header {
  display: flex;
  align-items: center;
  gap: 10px; /* Space between the logo and title */
}

.app-logo {
  width: 80px; /* Adjust size as needed */
  height: auto;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px 0;
  border-bottom: 3px solid var(--primary-color);
  position: relative;
}

.app-header::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(to right, var(--primary-color), var(--purple-color));
}

.app-title {
  text-align: center;
  flex-grow: 1;
  font-size: 2.8em;
  background: linear-gradient(45deg, var(--primary-color), var(--purple-color));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

/* Bell Button Styles */
.bell-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 28px;
  color: var(--primary-color);
  transition: all 0.3s ease;
  padding: 12px;
  border-radius: 50%;
  position: relative;
}

.bell-button:hover {
  transform: scale(1.1) rotate(15deg);
  color: var(--primary-light);
}

.bell-button.paused {
  color: var(--text-muted);
  opacity: 0.7;
}

.bell-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--primary-color);
  border-radius: 50%;
  z-index: -1;
  opacity: 0;
  transition: all 0.3s ease;
}

.bell-button:hover::after {
  opacity: 0.1;
  transform: scale(1.2);
}

/* Tab Styles */
.tabs {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
  padding: 0 10px;
}

.tab-button {
  padding: 12px 25px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.1em;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.tab-button.active {
  background: linear-gradient(45deg, var(--primary-color), var(--primary-light));
  color: white;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

.tab-button:not(.active) {
  background-color: var(--background-dark);
  color: var(--text-color);
}

.tab-button:hover {
  transform: translateY(-2px);
}

.tab-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, var(--primary-light), var(--purple-light));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tab-button:hover::before {
  opacity: 0.1;
}

/* Form Styles */
.task-form, .birthday-form {
  background: white;
  padding: 30px;
  border-radius: 16px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  margin-bottom: 30px;
}

.form-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: var(--text-color);
}

.input-field, .select-field {
  width: 100%;
  padding: 12px;
  border: 2px solid var(--background-dark);
  border-radius: 8px;
  font-size: 1em;
  transition: all 0.3s ease;
}

.input-field:focus, .select-field:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
  outline: none;
}

/* Task Card Styles */
.task-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
}

.task-card {
  background: white;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.task-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.task-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
}

.priority-badge {
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: 600;
  text-transform: uppercase;
}

.priority-badge.high {
  background-color: var(--danger-color);
  color: white;
}

.priority-badge.medium {
  background-color: var(--warning-color);
  color: var(--text-color);
}

.priority-badge.low {
  background-color: var(--success-color);
  color: white;
}

/* Birthday Card Styles */
.birthday-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.birthday-card {
  background: white;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.birthday-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.birthday-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.birthday-icon {
  font-size: 24px;
  color: var(--purple-color);
}

.birthday-name {
  font-size: 1.2em;
  font-weight: 600;
  color: var(--text-color);
}

.birthday-date {
  font-size: 1.1em;
  color: var(--text-muted);
  margin-bottom: 15px;
}

/* Button Styles */
.submit-button, .preview-button {
  background: linear-gradient(45deg, var(--primary-color), var(--primary-light));
  color: white;
  border: none;
  padding: 12px 25px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button:hover, .preview-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

/* Action Buttons */
.task-actions, .birthday-actions {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

.edit-button, .delete-button, .snooze-button, .complete-button {
  padding: 8px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.edit-button {
  background-color: var(--info-color);
  color: white;
}

.delete-button {
  background-color: var(--danger-color);
  color: white;
}

.snooze-button {
  background-color: var(--warning-color);
  color: var(--text-color);
}

.complete-button {
  background-color: var(--success-color);
  color: white;
}

/* Chart Container */
.chart-container {
  background: white;
  padding: 30px;
  border-radius: 16px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  margin-top: 30px;
}

.chart-container h2 {
  color: var(--text-color);
  margin-bottom: 20px;
  font-size: 1.5em;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  .app-container {
    padding: 20px;
  }

  .form-grid {
    grid-template-columns: 1fr;
  }

  .task-grid, .birthday-grid {
    grid-template-columns: 1fr;
  }

  .app-title {
    font-size: 2em;
  }
}
/* Task Filters Container */
.task-filters {
  background: white;
  padding: 25px;
  border-radius: 16px;
  margin: 20px 0;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  align-items: center;
}

/* Individual Filter Group */
.filter-group {
  flex: 1;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.filter-group label {
  font-weight: 600;
  color: var(--text-color);
  font-size: 0.9em;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.filter-select {
  padding: 12px;
  border: 2px solid var(--background-dark);
  border-radius: 8px;
  background-color: white;
  color: var(--text-color);
  font-size: 1em;
  transition: all 0.3s ease;
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 16px;
  padding-right: 40px;
}

.filter-select:hover {
  border-color: var(--primary-light);
}

.filter-select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
  outline: none;
}

/* Status Badge Styles */
.status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.85em;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.in-progress {
  background-color: var(--primary-color);
  color: white;
}

.status-badge.completed {
  background-color: var(--success-color);
  color: white;
}

.status-badge.overdue {
  background-color: var(--danger-color);
  color: white;
}

.status-badge.in-review {
  background-color: var(--warning-color);
  color: var(--text-color);
}

.status-badge.canceled {
  background-color: var(--text-muted);
  color: white;
}

/* Responsive Design for Filters */
@media (max-width: 768px) {
  .task-filters {
    flex-direction: column;
    padding: 20px;
  }

  .filter-group {
    width: 100%;
  }

  .filter-select {
    width: 100%;
  }
}

/* Active Filter Indicator */
.filter-group.active label {
  color: var(--primary-color);
}

.filter-group.active .filter-select {
  border-color: var(--primary-color);
  background-color: rgba(52, 152, 219, 0.05);
}

/* Filter Count Badge */
.filter-count {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  height: 24px;
  padding: 0 8px;
  border-radius: 12px;
  background-color: var(--primary-color);
  color: white;
  font-size: 0.85em;
  font-weight: 600;
  margin-left: 8px;
}

/* Clear Filters Button */
.clear-filters {
  padding: 8px 16px;
  border: 2px solid var(--primary-color);
  border-radius: 8px;
  background-color: transparent;
  color: var(--primary-color);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.clear-filters:hover {
  background-color: var(--primary-color);
  color: white;
}

/* Filter Section Title */
.filter-section-title {
  font-size: 1.1em;
  font-weight: 600;
  color: var(--text-color);
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.filter-section-title::before {
  content: '';
  width: 4px;
  height: 20px;
  background-color: var(--primary-color);
  border-radius: 2px;
  display: inline-block;
}

/* Style for the container */
.file-upload {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border: 2px dashed #6c63ff; /* Dashed border for a modern look */
  border-radius: 10px;
  background-color: #f9f9ff; /* Light background color */
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.file-upload:hover {
  background-color: #f0f0ff; /* Slightly darker shade on hover */
  border-color: #5a54e8;
}

/* Style for the label */
.file-upload label {
  font-size: 1.2rem;
  font-weight: bold;
  color: #6c63ff;
  margin-bottom: 10px;
  cursor: pointer;
}

/* Style for the file input */
.file-input {
  font-size: 0.8rem;
  padding: 5px 10px;
  color: #333;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.file-input:hover {
  border-color: #6c63ff;
}

.file-input:focus {
  border-color: #5a54e8;
  box-shadow: 0 0 5px rgba(92, 85, 232, 0.5);
}

/* Add a subtle animation for attention */
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 rgba(92, 85, 232, 0.5);
  }
  100% {
    box-shadow: 0 0 20px rgba(92, 85, 232, 0);
  }
}

.file-upload:hover label {
  animation: pulse 1s infinite;
}

/* Style for the custom button */
.custom-file-label {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #6c63ff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

/* Hover effect */
.custom-file-label:hover {
  background-color: #5a54e8;
  box-shadow: 0 0 10px rgba(92, 85, 232, 0.5);
}

/* Focus effect */
.custom-file-label:focus {
  outline: none;
  background-color: #7d74ff;
  box-shadow: 0 0 15px rgba(92, 85, 232, 0.8);
}
